import React from "react";
import { Input, FormGroup, Col } from "reactstrap";

function Documentacao({ inputDocumentation, setInputDocumentation }) {
  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputDocumentation({ ...inputDocumentation, [name]: value });
  };

  return (
    <div className="row">
      <div className="col-12">
        <h3>Informaes Gerais</h3>
        <hr />
      </div>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="documentation_area">
            rea na Documentao (ha)
          </label>
          <Input
            className="form-control-alternative"
            type="number"
            name="documentation_area"
            title="rea na Documentao (ha)"
            placeholder="Ex: 1.500"
            value={inputDocumentation.documentation_area}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="property_name">
            Nome da Propriedade
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="property_name"
            title="Nome da Propriedade"
            placeholder="Ex: Chcara So Pedro"
            value={inputDocumentation.property_name}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="georeferenced">
            Georreferenciada
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDocumentation.georeferenced}
            title="Georreferenciada"
            name="georeferenced"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="property_ownership">
            Propriedade / Posse / Concesso
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            name="property_ownership"
            title="Propriedade / Posse / Concesso"
            placeholder="Propriedade / Posse / Concesso"
            value={inputDocumentation.property_ownership}
            onChange={handleChangeInput}
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value="Propriedades">Propriedades</option>
            <option value="Posse">Posse</option>
            <option value="Concesso">Concesso</option>
          </Input>
        </FormGroup>
      </Col>
      <div className="col-12">
        <h3>Documentao - Propriedade - Somente para Propriedade</h3>
        <hr />
      </div>
      <div className="col-12">
        <h3>Documentao - Concesso - Tipo de Documento</h3>
        <hr />
      </div>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="domain_title">
            Ttulo de Domnio
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDocumentation.domain_title}
            title="Ttulo de Domnio"
            name="domain_title"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="ccu">
            Contrato de Concesso de Uso - CCU
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDocumentation.ccu}
            title="Contrato de Concesso de Uso - CCU"
            name="ccu"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="ccdru">
            Contrato de Concesso de Direito Real de Uso - CCDRU
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDocumentation.ccdru}
            title="Contrato de Concesso de Direito Real de Uso - CCDRU"
            name="ccdru"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6" className="d-flex align-items-end">
        <FormGroup className="w-100">
          <label className="form-control-label" htmlFor="regularization">
            Em Regularizao
          </label>
          <Input
            className="form-control-alternative d-inline"
            type="select"
            onChange={handleChangeInput}
            value={inputDocumentation.regularization}
            title="Em Regularizao"
            name="regularization"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <div className="col-12">
        <h3>Documentao - Propriedades e Concesses</h3>
        <hr />
      </div>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="sncr">
            Cdigo de Sistema Nacional de Cadastro Rural (SNCR)
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="sncr"
            title="Cdigo de Sistema Nacional de Cadastro Rural (SNCR)"
            placeholder="Ex: 0000000"
            value={inputDocumentation.sncr}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="incra_certificate">
            Certificao do Imvel no INCRA
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="incra_certificate"
            title="Certificao do Imvel no INCRA"
            placeholder="Ex: 0000"
            value={inputDocumentation.incra_certificate}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
    </div>
  );
}

export default Documentacao;
