import React from "react";
import { Input, FormGroup, Col, Row } from "reactstrap";

function InfoDoImovel({
  inputPropertyInformation,
  setInputPropertyInformation,
}) {
  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputPropertyInformation({ ...inputPropertyInformation, [name]: value });
  };

  return (
    <Row>
      <Col lg="12">
        <label className="form-control-label" htmlFor="join_pra">
          Deseja aderir ao Programa de Regularizao Ambiental - PRA, caso o
          imvel rural possua (uma das situaes a seguir, ocorrida at 22 de
          julho de 2008): necessidade de recomposio de reas de APP e de uso
          restrito; dficit referente a Reserva Legal; autuao?
        </label>
      </Col>
      <Col lg="6">
        <FormGroup>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            name="join_pra"
            value={inputPropertyInformation.join_pra}
            title="Deseja aderir ao Programa de Regularizao Ambiental - PRA, caso o
          imvel rural possua (uma das situaes a seguir, ocorrida at 22 de
          julho de 2008): necessidade de recomposio de reas de APP e de uso
          restrito; dficit referente a Reserva Legal; autuao?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="12">
        <label className="form-control-label" htmlFor="has_area_with_deficit_native_vegetation">
          O imvel rural possu rea com dficit de vegetao nativa para fim de
          cumprimento da Reserva Legal?
        </label>
      </Col>
      <Col lg="6">
        <FormGroup>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            name="has_area_with_deficit_native_vegetation"
            value={inputPropertyInformation.has_area_with_deficit_native_vegetation}
            title="O imvel rural possu rea com dficit de vegetao nativa para fim de 
          cumprimento da Reserva Legal?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="12">
        <FormGroup>
          <label className="form-control-label" htmlFor="alternatives_regularize_deficit">
            Qual alternativa voc pretende adotar, isolada ou conjuntamente,
            para regularizar o dficit?
          </label>
          <Input
            className="form-control-alternative"
            type="textarea"
            name="alternatives_regularize_deficit"
            rows="6"
            title="Qual alternativa voc pretende adotar, isolada ou conjuntamente, para
          regularizar o dficit?"
            onChange={handleChangeInput}
          />
        </FormGroup>
      </Col>
      <Col lg="12">
        <FormGroup>
          <label className="form-control-label" htmlFor="how_want_make_up_deficit_area">
            Caso realize a compensao, como deseja compensar a rea com
            deficit?
          </label>
          <Input
            className="form-control-alternative"
            type="textarea"
            name="how_want_make_up_deficit_area"
            value={inputPropertyInformation.how_want_make_up_deficit_area}
            rows="6"
            title="Caso realize a compensao, como deseja compensar a rea com deficit?"
            onChange={handleChangeInput}
          />
        </FormGroup>
      </Col>
      <Col lg="12">
        <label className="form-control-label" htmlFor="has_tac_with_app">
          Existe Termo de Ajuste de Conduta (TAC) aprovado referente 
          regularizao de reas de Preservao Permanente (APP), Reserva Legal
          ou rea de Uso Restrito do Imvel, objeto de Autuao?
        </label>
      </Col>
      <Col lg="6">
        <FormGroup>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputPropertyInformation.has_tac_with_app}
            name="tac"
            title="Existe Termo de Ajuste de Conduta (TAC) aprovado referente 
          regularizao de reas de Preservao Permanente (APP), Reserva Legal ou
          rea de Uso Restrito do Imvel, objeto de Autuao?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="12">
        <label className="form-control-label" htmlFor="has_infractions_committed">
          Existem infraes cometidas anteriores a 22 de Julho de 2008,
          relativas a supresso irregular de vegetao em reas de Preservao
          Permanente (APP), Reserva Legal ou rea de Uso Restrito do Imvel,
          objeto de Autuao?
        </label>
      </Col>
      <Col lg="6">
        <FormGroup>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputPropertyInformation.has_infractions_committed}
            name="has_infractions_committed"
            title="Existem infraes cometidas anteriores a 22 de Julho de 2008, relativas
          a supresso irregular de vegetao em reas de Preservao Permanente
          (APP), Reserva Legal ou rea de Uso Restrito do Imvel, objeto de
          Autuao?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="12">
        <label className="form-control-label" htmlFor="property_has_arvn">
          O imvel rural possu rea Remanescente de Vegetao Nativa excedente
          ao mnimo exigido para Reserva Legal?
        </label>
      </Col>
      <Col lg="6">
        <FormGroup>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputPropertyInformation.property_has_arvn}
            name="property_has_arvn"
            title="O imvel rural possu rea Remanescente de Vegetao Nativa excedente ao
          mnimo exigido para Reserva Legal?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="12">
        <label className="form-control-label" htmlFor="what_wantto_with_surplus_area">
          O que voc deseja fazer com a rea excedente?
        </label>
      </Col>
      <Col lg="12">
        <FormGroup>
          <Input
            className="form-control-alternative"
            type="textarea"
            name="what_wantto_with_surplus_area"
            value={inputPropertyInformation.what_wantto_with_surplus_area}
            rows="6"
            title="O que voc deseja fazer com a rea excedente?"
            onChange={handleChangeInput}
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="has_rppn">
            Existe Reserva Particular do Patrimnio Natural (RPPN) no interior
            do imvel rural?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputPropertyInformation.has_rppn}
            name="has_rppn"
            title="Existe Reserva Particular do Patrimnio Natural (RPPN) no interior do imvel rural?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6" className="d-flex align-items-end">
        <FormGroup className="w-100">
          <label className="form-control-label" htmlFor="has_crf">
            Possu Cota de Reverva Florestal (CRF)?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputPropertyInformation.has_crf}
            name="has_crf"
            title="Possu Cota de Reverva Florestal (CRF)?"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="rli_period">
            A Reserva Legal do Imvel rural est submetida  legislao de que
            perodo?
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="rli_period"
            title="A Reserva Legal do Imvel rural est submetida  legislao de que perodo?"
            placeholder="Ex: 21/01/2002 a 10/03/2012"
            value={inputPropertyInformation.rli_period}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>

      <Col lg="6" className="d-flex align-items-end">
        <FormGroup className="w-100">
          <label className="form-control-label" htmlFor="size_changes_after">
            Ocorreu alterao no tamanho da rea do imvel aps 22/07/2008?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputPropertyInformation.size_changes_after}
            title="Ocorreu alterao no tamanho da rea do imvel aps 22/07/2008?"
            name="size_changes_after"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
    </Row>
  );
}

export default InfoDoImovel;
